import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "1.0.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>새로운 기능</Title>
                    <Line/>
                    생방송 다운로드 기능
                    <br/>
                    비디오 다운로드 기능
                    <br/>
                    클립 다운로드 기능
                    <br/>
                    <Important>구독자 전용 다시보기 다운로드 기능</Important>
                    <br/>
                    <Important>음소거 해제 기능</Important>
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}