import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "1.0.2";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>New features and improvements</Title>
                    <Line/>
                    <Important>You can log in or change your account immediately if you do not have permissions to download subscriber only videos.</Important>
                    <br/>
                    Improved Update Tracking mode.
                    <br/>
                    <Important>New feature has been added to protect the rights of streamers.</Important>
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}