import React, { Component } from "react";

import { Link } from "react-router-dom";

import './Versions.css';

import Content from '../../components/Contents/index.js';
import Button from '../../components/Buttons/index.js';
import Label from '../../components/Labels/Label/Label.js';

import CopyToClipboard from "../../utils/clipboard.js";

import UpdateNote_1_0_0_ko from '../../UpdateNotes/1.0.0/ko.js';
import UpdateNote_1_0_0_en from '../../UpdateNotes/1.0.0/en.js';
import UpdateNote_1_0_1_ko from '../../UpdateNotes/1.0.1/ko.js';
import UpdateNote_1_0_1_en from '../../UpdateNotes/1.0.1/en.js';
import UpdateNote_1_0_2_ko from '../../UpdateNotes/1.0.2/ko.js';
import UpdateNote_1_0_2_en from '../../UpdateNotes/1.0.2/en.js';
import UpdateNote_1_0_3_ko from '../../UpdateNotes/1.0.3/ko.js';
import UpdateNote_1_0_3_en from '../../UpdateNotes/1.0.3/en.js';
import UpdateNote_1_0_4_ko from '../../UpdateNotes/1.0.4/ko.js';
import UpdateNote_1_0_4_en from '../../UpdateNotes/1.0.4/en.js';
import UpdateNote_1_1_0_ko from '../../UpdateNotes/1.1.0/ko.js';
import UpdateNote_1_1_0_en from '../../UpdateNotes/1.1.0/en.js';

export default class Version extends Component {
  UpdateFiles = {
    "1.0.0": {"en": UpdateNote_1_0_0_en, "ko": UpdateNote_1_0_0_ko},
    "1.0.1": {"en": UpdateNote_1_0_1_en, "ko": UpdateNote_1_0_1_ko},
    "1.0.2": {"en": UpdateNote_1_0_2_en, "ko": UpdateNote_1_0_2_ko},
    "1.0.3": {"en": UpdateNote_1_0_3_en, "ko": UpdateNote_1_0_3_ko},
    "1.0.4": {"en": UpdateNote_1_0_4_en, "ko": UpdateNote_1_0_4_ko},
    "1.1.0": {"en": UpdateNote_1_1_0_en, "ko": UpdateNote_1_1_0_ko}
  };

  T(en, ko) {
    if (this.props.language == "en") {
      return en;
    }
    else {
      return ko;
    }
  }

  copyLink(url) {
    CopyToClipboard(url);
    alert(this.T("Copied to clipboard", "클립보드에 복사되었습니다."));
  }

  render() {
    var DataFile = this.UpdateFiles[this.props.version][this.props.language];
    var UpdateTitle = DataFile.UpdateTitle;
    var UpdateNote = DataFile.UpdateNote;
    var SourceUrl = DataFile.SourceUrl;
    return (
      <Content.Area className="Versions">
        <Content.Box>
          <Content.Subtitle>
            <UpdateTitle />
          </Content.Subtitle>
          <Content.TextArea>
            <UpdateNote />
          </Content.TextArea>
        </Content.Box>
        {this.props.underDevelopment ?
          <Label style={{padding: "5px 15px"}}>
            {this.T("The above version is currently under development.", "위 버전은 현재 개발 중입니다.")}
          </Label>
        :
        <div>
          <Content.SrcViewer>
            <div>
              {SourceUrl}
            </div>
          </Content.SrcViewer>
          <Button.ButtonArea>
            <Button.LinkButton href={SourceUrl} target="_blank">{this.T("Source Code", "소스코드")}</Button.LinkButton>
            <Button.Button onClick={() => this.copyLink(SourceUrl)}>{this.T("Share", "공유")}</Button.Button>
            <Link className="Button" style={{color: "black", textDecoration: "none"}} to={"/donate?download=" + this.props.version}>{this.T("Download", "다운로드")}</Link>
            <Label>v {this.props.version}</Label>
          </Button.ButtonArea>
        </div>
        }
      </Content.Area>
    );
  }
};