import React, { Component } from "react";

import "./DownloadPage.css";

import Adsense from "../../components/Adsense/Adsense.js";
import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Versions from "../../components/Versions/Versions.js";

export default class DownloadPage extends Component {
  T(en, ko) {
    if (this.props.language == "en") {
      return en;
    }
    else {
      return ko;
    }
  }

  render() {
    return (
      <div className="DownloadPage">
        <PageFrame>
          <Content.TitleBar>{this.T("Releases", "버전 목록")}</Content.TitleBar>
          <Adsense
            name="TwitchLink_Download_1"
            style={{"display": "block"}}
            dataAdSlot="7999594816"
            dataAdFormat="auto"
            dataFullWidthResponsive="true"
          />
          <Versions version="1.1.0" language={this.props.language} underDevelopment />
          <Versions version="1.0.4" language={this.props.language} />
          <Versions version="1.0.3" language={this.props.language} />
          <Adsense
            name="TwitchLink_Download_2"
            style={{"display": "block"}}
            dataAdSlot="1937922802"
            dataAdFormat="auto"
            dataFullWidthResponsive="true"
          />
          <Versions version="1.0.2" language={this.props.language} />
          <Versions version="1.0.1" language={this.props.language} />
          <Versions version="1.0.0" language={this.props.language} />
        </PageFrame>
      </div>
    );
  }
}