import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "1.0.2";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>새로운 기능 및 개선</Title>
                    <Line/>
                    <Important>이제 구독자 전용 비디오 다운로드 시 권한이 부족하면 바로 로그인하거나 계정을 변경할 수 있습니다.</Important>
                    <br/>
                    업데이트 추적 모드가 개선되었습니다.
                    <br/>
                    <Important>스트리머의 권리 보호를 위한 기능이 추가되었습니다.</Important>
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}