import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "1.0.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>New Features</Title>
                    <Line/>
                    Can download streams.
                    <br/>
                    Can download videos.
                    <br/>
                    Can download clips.
                    <br/>
                    <Important>Can download subscribers-only videos.</Important>
                    <br/>
                    <Important>Can unmute videos.</Important>
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}